:root {
  --swiper-theme-color: #007aff;
}

.swiper-container {
  margin-left: auto;
  margin-right: auto;
  position: relative;
  overflow: hidden;
  list-style: none;
  padding: 0;
  /* Fix of Webkit flickering */
  z-index: 1;
}

.swiper-container-vertical > .swiper-wrapper {
  flex-direction: column;
}

.swiper-wrapper {
  position: relative;
  width: 100%;
  height: 100%;
  z-index: 1;
  display: flex;
  transition-property: transform;
  box-sizing: content-box;
}

.swiper-container-android .swiper-slide,
.swiper-wrapper {
  transform: translate3d(0px, 0, 0);
}

.swiper-container-multirow > .swiper-wrapper {
  flex-wrap: wrap;
}

.swiper-container-multirow-column > .swiper-wrapper {
  flex-wrap: wrap;
  flex-direction: column;
}

.swiper-container-free-mode > .swiper-wrapper {
  transition-timing-function: ease-out;
  margin: 0 auto;
}

.swiper-container-pointer-events {
  touch-action: pan-y;
}
.swiper-container-pointer-events.swiper-container-vertical {
  touch-action: pan-x;
}

.swiper-slide {
  flex-shrink: 0;
  width: 100%;
  height: 100%;
  position: relative;
  transition-property: transform;
}

.swiper-slide-invisible-blank {
  visibility: hidden;
}

/* Auto Height */
.swiper-container-autoheight,
.swiper-container-autoheight .swiper-slide {
  height: auto;
}
.swiper-container-autoheight .swiper-wrapper {
  align-items: flex-start;
  transition-property: transform, height;
}

/* 3D Effects */
.swiper-container-3d {
  perspective: 1200px;
}
.swiper-container-3d .swiper-wrapper,
.swiper-container-3d .swiper-slide,
.swiper-container-3d .swiper-slide-shadow-left,
.swiper-container-3d .swiper-slide-shadow-right,
.swiper-container-3d .swiper-slide-shadow-top,
.swiper-container-3d .swiper-slide-shadow-bottom,
.swiper-container-3d .swiper-cube-shadow {
  transform-style: preserve-3d;
}
.swiper-container-3d .swiper-slide-shadow-left,
.swiper-container-3d .swiper-slide-shadow-right,
.swiper-container-3d .swiper-slide-shadow-top,
.swiper-container-3d .swiper-slide-shadow-bottom {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
  z-index: 10;
}
.swiper-container-3d .swiper-slide-shadow-left {
  background-image: linear-gradient(to left, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0));
}
.swiper-container-3d .swiper-slide-shadow-right {
  background-image: linear-gradient(to right, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0));
}
.swiper-container-3d .swiper-slide-shadow-top {
  background-image: linear-gradient(to top, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0));
}
.swiper-container-3d .swiper-slide-shadow-bottom {
  background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0));
}

/* CSS Mode */
.swiper-container-css-mode > .swiper-wrapper {
  overflow: auto;
  scrollbar-width: none;
  /* For Firefox */
  -ms-overflow-style: none;
  /* For Internet Explorer and Edge */
}
.swiper-container-css-mode > .swiper-wrapper::-webkit-scrollbar {
  display: none;
}
.swiper-container-css-mode > .swiper-wrapper > .swiper-slide {
  scroll-snap-align: start start;
}

.swiper-container-horizontal.swiper-container-css-mode > .swiper-wrapper {
  scroll-snap-type: x mandatory;
}

.swiper-container-vertical.swiper-container-css-mode > .swiper-wrapper {
  scroll-snap-type: y mandatory;
}

/* main */
body {
  font-size: 16px;
  font-weight: 400;
  font-family: "SuisseIntl", sans-serif;
  color: #484848;
  background-color: #ffffff;
}

header,
main,
footer {
  padding-left: 128px;
}
@media only screen and (max-width: 1279px) {
  header,
main,
footer {
    padding-left: 0;
  }
}

a {
  text-decoration: none;
  color: #484848;
}

ul,
ol,
nav {
  list-style: none;
  padding: 0;
}

h1 {
  font-weight: 400;
}

input,
button {
  -webkit-appearance: none;
}

input,
button,
select,
textarea {
  font: inherit;
  background-color: transparent;
  border: 0;
  border-radius: 0;
  outline: 0;
  padding: 0;
  margin: 0;
}

input::placeholder,
textarea::placeholder {
  font-family: "SuisseIntl", sans-serif;
}
input:focus::placeholder,
textarea:focus::placeholder {
  opacity: 0;
}

.container {
  max-width: 100%;
  width: 1710px;
  padding: 0 30px;
  margin: 0 auto;
}
@media only screen and (max-width: 1279px) {
  .container {
    padding: 0 18px;
  }
}
.container--full {
  width: 100%;
  padding: 0 48px 0 56px;
}
@media only screen and (max-width: 1599px) {
  .container--full {
    padding: 0 18px;
  }
}

.body-scroll-lock {
  overflow: hidden;
}

.topic {
  font-size: 13px;
  line-height: 15px;
  font-weight: 600;
  letter-spacing: 0.025em;
  text-transform: uppercase;
  color: #ffffff;
}
@media only screen and (max-width: 767px) {
  .topic {
    font-size: 10px;
    line-height: 12px;
  }
}
.topic--dark {
  color: #484848;
}

.white-text {
  color: #ffffff !important;
}

/* Select2 custom */
.select2-container--default.select2-container--open .select2-selection--single .select2-selection__arrow b {
  border: 0;
}
.select2-container--default .select2-selection--single {
  background-color: transparent;
  border: 0;
}
.select2-container--default .select2-selection--single .select2-selection__rendered {
  font-family: "HelveticaNeueCyr", sans-serif;
  font-size: 13px;
  font-weight: 700;
  letter-spacing: 0.025em;
  color: #484848;
  text-transform: uppercase;
}
.select2-container--default .select2-selection--single .select2-selection__placeholder {
  font-size: 16px;
  letter-spacing: -0.02em;
  color: #ffffff;
}
.select2-container--default .select2-selection--single .select2-selection__arrow {
  width: 10px;
  height: 6px;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}
.select2-container--default .select2-selection--single .select2-selection__arrow b {
  width: 100%;
  height: 100%;
  display: block;
  margin: 0;
  border: none;
  position: relative;
  top: 0;
  left: 0;
  background-image: url("data:image/svg+xml;charset=UTF-8,%3csvg width='10' height='6' viewBox='0 0 10 6' fill='none' xmlns='http://www.w3.org/2000/svg'%3e%3cpath d='M1 1L5 5L9 1' stroke='%23484848' stroke-width='1.5' stroke-linecap='round' stroke-linejoin='round'/%3e%3c/svg%3e ");
  background-repeat: no-repeat;
  background-position: center;
}

/* components */
.button {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  padding: 28.5px 42px;
  background-color: rgba(39, 101, 192, 0.1);
  border-radius: 67px;
  font-size: 13px;
  line-height: 15px;
  font-weight: 600;
  letter-spacing: 0.025em;
  text-transform: uppercase;
  color: #265fc1;
  cursor: pointer;
  transition: all 0.3s ease;
}
@media only screen and (max-width: 767px) {
  .button {
    font-size: 10px;
    line-height: 12px;
  }
}
@media (min-width: 1080px) {
  .button:hover, .button:focus {
    background-color: rgba(39, 101, 192, 0.2);
  }
}
.button--green {
  background-color: #43cda3;
  color: #ffffff;
}
@media (min-width: 1080px) {
  .button--green:hover, .button--green:focus {
    background-color: rgba(67, 205, 163, 0.8);
  }
}
.button--blue {
  background-color: #2765c0;
  color: #ffffff;
}
@media (min-width: 1080px) {
  .button--blue:hover, .button--blue:focus {
    background-color: rgba(39, 101, 192, 0.8);
  }
}

.menu {
  display: flex;
  padding: 0 20px;
}
@media only screen and (max-width: 1279px) {
  .menu {
    display: none;
  }
}
.menu li + li {
  margin-left: 63px;
}
@media only screen and (max-width: 1599px) {
  .menu li + li {
    margin-left: 35px;
  }
}
.menu li a {
  font-size: 13px;
  line-height: 15px;
  font-weight: 600;
  text-transform: uppercase;
  transition: opacity 0.3s ease;
}
@media (min-width: 1080px) {
  .menu li a:hover, .menu li a:focus {
    opacity: 0.8;
  }
}

.lang-switcher {
  width: 100%;
  height: 78px;
  border-top: 1px solid rgba(0, 0, 0, 0.2);
}
.lang-switcher select {
  color: #ffffff;
}
.lang-switcher .select2-container--default {
  width: 100%;
  height: 100%;
}
.lang-switcher .select2-container--default .select2-selection--single {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
}
.lang-switcher .select2-container--default .select2-selection--single .select2-selection__rendered {
  display: flex;
  align-items: center;
  justify-content: center;
  padding-right: 10px;
  padding-left: 0;
}
.lang-switcher .select2-container--default .select2-selection--single .select2-selection__arrow {
  position: relative;
  top: unset;
  left: unset;
  margin-top: 5px;
}

.sidebar {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  width: 128px;
  height: 100vh;
  padding-top: 50px;
  background-color: #ffffff;
  border-right: 1px solid rgba(0, 0, 0, 0.2);
  position: fixed;
  top: 0;
  left: 0;
  z-index: 101;
}
.sidebar.open .sidebar__burger::before {
  transform: rotate(45deg) translate(5px, 0);
}
@media only screen and (max-width: 1279px) {
  .sidebar.open .sidebar__burger::before {
    transform: rotate(45deg) translate(0, 5px);
  }
}
.sidebar.open .sidebar__burger::after {
  transform: rotate(-45deg) translate(-5px, 0);
}
@media only screen and (max-width: 1279px) {
  .sidebar.open .sidebar__burger::after {
    transform: rotate(-45deg) translate(-3px, -4px);
  }
}
.sidebar.open .sidebar__menu {
  pointer-events: auto;
  opacity: 1;
}
@media only screen and (max-width: 1279px) {
  .sidebar {
    flex-direction: row;
    width: 100%;
    height: 50px;
    padding: 5px 18px 0;
    border-right: 0;
  }
}
.sidebar__smile {
  width: 31px;
  height: 31px;
}
@media only screen and (max-width: 1279px) {
  .sidebar__smile {
    width: 22px;
    height: 22px;
  }
}
.sidebar__smile svg {
  width: 100%;
  height: 100%;
}
.sidebar__burger-wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  flex-grow: 1;
  cursor: pointer;
}
@media only screen and (max-width: 1279px) {
  .sidebar__burger-wrapper {
    width: auto;
    height: 100%;
    flex-grow: unset;
  }
}
.sidebar__burger {
  display: flex;
  justify-content: space-between;
  width: 13px;
  height: 57px;
}
@media only screen and (max-width: 1279px) {
  .sidebar__burger {
    flex-direction: column;
    width: 41px;
    height: 7px;
  }
}
.sidebar__burger::before, .sidebar__burger::after {
  content: "";
  display: block;
  width: 4px;
  height: 100%;
  background-color: #484848;
  transition: transform 0.3s ease;
}
@media only screen and (max-width: 1279px) {
  .sidebar__burger::before, .sidebar__burger::after {
    width: 100%;
    height: 2px;
  }
}
@media only screen and (max-width: 1279px) {
  .sidebar__lang-switcher {
    display: none;
  }
}
.sidebar__lang-switcher select {
  width: 100%;
}
.sidebar__menu {
  position: fixed;
  top: 0;
  left: 128px;
  width: calc(38vw - 64px);
  height: 100vh;
  opacity: 0;
  pointer-events: none;
  transition: opacity 0.3s ease;
  background-color: #ffffff;
}
@media only screen and (max-width: 1279px) {
  .sidebar__menu {
    width: 100%;
    height: calc(100vh - 50px);
    top: 50px;
    left: 0;
  }
}
.sidebar__menu > li {
  height: calc(100% / 6);
}
@media only screen and (max-width: 1279px) {
  .sidebar__menu > li + li a::before {
    content: "";
    width: calc(100% - 112px);
    height: 1px;
    background-color: rgba(0, 0, 0, 0.2);
    position: absolute;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
  }
}
.sidebar__menu > li > a {
  display: flex;
  align-items: center;
  width: 100%;
  height: 100%;
  padding-left: 56px;
  font-family: "HelveticaNeueCyr", sans-serif;
  font-size: 48px;
  line-height: 48px;
  letter-spacing: -1.4px;
  color: rgba(72, 72, 72, 0.2);
  position: relative;
  transition: background-color 0.3s ease, color 0.3s ease;
}
@media (min-width: 1080px) {
  .sidebar__menu > li > a:hover, .sidebar__menu > li > a:focus {
    background-color: #255ec1;
    color: #ffffff;
  }
}
@media only screen and (max-width: 767px) {
  .sidebar__menu > li > a {
    font-size: 40px;
    line-height: 40px;
  }
}
@media only screen and (max-width: 767px) {
  .sidebar__menu > li > a {
    font-size: 35px;
    line-height: 35px;
  }
}
.sidebar__menu-inner {
  display: flex;
  width: calc(62vw - 64px);
  height: 100vh;
  position: absolute;
  top: 0;
  left: 100%;
  opacity: 0;
  pointer-events: none;
  transition: opacity 0.3s ease;
}
@media only screen and (max-width: 1279px) {
  .sidebar__menu-inner {
    display: none;
  }
}
.sidebar__menu-inner.active {
  opacity: 1;
  pointer-events: auto;
}
.sidebar__menu-inner ul + ul {
  margin-top: 63px;
}
.sidebar__menu-inner ul li:first-child {
  font-family: "HelveticaNeueCyr", sans-serif;
  font-size: 16px;
  line-height: 19px;
  color: rgba(255, 255, 255, 0.2);
  margin-bottom: 24px;
}
.sidebar__menu-inner ul li + li {
